import React from "react"
import { graphql, StaticQuery } from "gatsby"
import Img from "gatsby-image"

import Layout from "../components/layout"
import SEO from "../components/seo"

import "../utils/normalize.css"
import "../utils/css/screen.css"

const AboutPage = ({ data }, location) => {
  const siteTitle = data.site.siteMetadata.title

  return (
    <Layout title={siteTitle}>
      <SEO title="About" keywords={[`blog`, `myactiondiary`, `take action`, `react`]} />

      <article className="post-content page-template no-image">
        <div className="post-content-body">
          <h2 id="clean-minimal-and-deeply-customisable-london-is-a-theme-made-for-people-who-appreciate-simple-lines-">
            About Us
          </h2>
          <figure className="kg-card kg-image-card kg-width-full">
            <Img
              fluid={data.benchAccounting.childImageSharp.fluid}
              className="kg-image"
            />
            <figcaption></figcaption>
          </figure>
          <h3 id="dynamic-styles">We're getting our act together!</h3>
          <p>
            We're learning constructively, acting purposefully, and getting it done.
          </p>
          <p>
            This is a diary of actions we are taking to care for ourselves, serve our community, and live our truths.
          </p>
          <h3 id="dynamic-styles">What actions will you take today?</h3>
          <p>
            If you are ready to take action in building a more equitable, diverse, and inclusive world, prove it.
          </p>
          <p>
            Follow @myactiondiary on <a href="https://www.instagram.com/myactiondiary">Instagram</a> and/or <a href="https://www.facebook.com/myactiondiary">Facebook</a> and use #myactiondiary to share the actions you are taking in your day-to-day life to learn constructively, act purposefully, and live ethically.
          </p>
          <p>
            Subscribe here to receive 1 email every month containing stories, free resources, actions you can try out and more! We will not share your email information nor spam you.
          </p>
          <p>
            Thank you for being part of this one world with us.
          </p>
          <p>
            I look forward to seeing you in action.
          </p>
        </div>
      </article>
    </Layout>
  )
}

const indexQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    benchAccounting: file(
      relativePath: { eq: "MAD-logo-banner.jpg" }
    ) {
      childImageSharp {
        fluid(maxWidth: 1360) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`

export default props => (
  <StaticQuery
    query={indexQuery}
    render={data => (
      <AboutPage location={props.location} data={data} {...props} />
    )}
  />
)
